// import axios from "axios";

const state = {
  Name: "JS3 Register",
  dialog: false
};

const getters = {
  countFigure: state => state.Name,
  dial: state => state.dialog
};

const actions = {
  OpenDialogue({ commit }) {
    const dialo = !state.dialog;
    commit("addDialog", dialo);
  }
};

const mutations = {
  addDialog: (state, d) => (state.dialog = d)
};

export default {
  state,
  getters,
  actions,
  mutations
};
